:root {
    --ss-error-color: #dc3545;
    --ss-main-height: 35px;
    --ss-content-height: 500px;
    --ss-spacing-l: 7px;
    --ss-spacing-m: 5px;
    --ss-spacing-s: 3px;
    --ss-animation-timing: 0.2s;
    --ss-border-radius: 8px;
}

@keyframes ss-valueIn {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes ss-valueOut {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(0);
        opacity: 0;
    }
}

.ss-hide {
    display: none !important;
}

.ss-main {
    display: flex;
    flex-direction: row;
    position: relative;
    min-height: var(--ss-main-height);
    width: 100%;
    padding: var(--ss-spacing-s);
    cursor: pointer;
    outline: 0;
    box-sizing: border-box;
    transition: background-color var(--ss-animation-timing);
    overflow: hidden;
}

.ss-main:focus {
    box-shadow: 0 0 5px var(--primary-color);
}

.ss-main.ss-disabled {
    background-color: var(--ss-disabled-color);
    cursor: not-allowed;
}

.ss-main.ss-disabled .ss-values .ss-value .ss-value-delete {
    cursor: not-allowed;
}

.ss-main.ss-open-above {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.ss-main.ss-open-below {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
}

.ss-main .ss-values {
    display: inline-flex;
    flex-wrap: wrap;
    gap: var(--ss-spacing-m);
    flex: 1 1 100%;
}

.ss-main .ss-values .ss-placeholder {
    display: flex;
    line-height: 1em;
    align-items: center;
    width: 100%;
    color: var(--light-theme-main-text);
    opacity: 0.5;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ss-main .ss-values .ss-max {
    display: flex;
    user-select: none;
    align-items: center;
    width: fit-content;
    font-size: 12px;
    color: var(--dark-text-color);
    line-height: 1;
    padding: var(--ss-spacing-s) var(--ss-spacing-m);
    background-color: var(--primary-color);
    border-radius: var(--ss-border-radius);
}

.ss-main .ss-values .ss-single {
    display: flex;
    margin: auto 0px auto var(--ss-spacing-s);
}

.ss-main .ss-values .ss-value {
    display: flex;
    user-select: none;
    align-items: center;
    width: fit-content;
    background-color: var(--primary-color);
    border-radius: var(--ss-border-radius);
    animation-name: ss-valueIn;
    animation-duration: var(--ss-animation-timing);
    animation-timing-function: ease-out;
    animation-fill-mode: both;
}

.ss-main .ss-values .ss-value.ss-value-out {
    animation-name: ss-valueOut;
    animation-duration: var(--ss-animation-timing);
    animation-timing-function: ease-out;
}

.ss-main .ss-values .ss-value .ss-value-text {
    font-size: 12px;
    color: var(--dark-text-color);
    line-height: 1;
    padding: var(--ss-spacing-s) var(--ss-spacing-m);
}

.ss-main .ss-values .ss-value .ss-value-delete {
    display: flex;
    align-items: center;
    height: var(--ss-spacing-l);
    width: var(--ss-spacing-l);
    padding: var(--ss-spacing-s) var(--ss-spacing-m);
    cursor: pointer;
    border-left: solid 1px var(--primary-color);
    box-sizing: content-box;
}

.ss-main .ss-values .ss-value .ss-value-delete svg {
    height: var(--ss-spacing-l);
    width: var(--ss-spacing-l);
}

.ss-main .ss-values .ss-value .ss-value-delete svg path {
    fill: none;
    stroke: var(--dark-text-color);
    stroke-width: 18;
    stroke-linecap: round;
    stroke-linejoin: round;
}

.ss-main .ss-deselect {
    display: flex;
    align-self: center;
    justify-content: flex-end;
    flex: 0 1 auto;
    width: 8px;
    height: 8px;
    margin: 0 var(--ss-spacing-m) 0 var(--ss-spacing-m);
}

.ss-main .ss-deselect svg {
    width: 8px;
    height: 8px;
}

.ss-main .ss-deselect svg path {
    fill: none;
    stroke-width: 20;
    stroke-linecap: round;
    stroke-linejoin: round;
}

.ss-main .ss-arrow {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 0 1 auto;
    width: 20px;
    height: 20px;
    margin: auto var(--ss-spacing-m) auto var(--ss-spacing-m);
}

.ss-main .ss-arrow path {
    stroke: var(--dark-text-color);
    opacity: 0.7;
    stroke-width: 2px;
    stroke-linecap:round;
    stroke-linejoin: round;
    transition-timing-function: ease-out;
    transition: var(--ss-animation-timing);
}

.ss-content {
    position: absolute;
    display: flex;
    height: auto;
    flex-direction: column;
    width: auto;
    max-height: var(--ss-content-height);
    box-sizing: border-box;
    background-color: var(--light-text-color);
    transition: transform var(--ss-animation-timing), opacity var(--ss-animation-timing);
    opacity: 0;
    transform: scaleY(0);
    transform-origin: center top;
    overflow: hidden;
    z-index: 10000;
}

.ss-content.ss-relative {
    position: relative;
    height: 100%;
}

.ss-content.ss-open-above {
    flex-direction: column-reverse;
    opacity: 1;
    transform: scaleY(1);
    transform-origin: center bottom;
    border-top-left-radius: var(--ss-border-radius);
    border-top-right-radius: var(--ss-border-radius);
}

.ss-content.ss-open-below {
    opacity: 1;
    transform: scaleY(1);
    transform-origin: center top;
    height: auto;
    box-shadow: 0 0 5px var(--primary-color);
}
    


.ss-content .ss-search {
    flex: 0 1 auto;
    display: flex;
    flex-direction: row;
    padding: var(--ss-spacing-l) var(--ss-spacing-l) var(--ss-spacing-m) var(--ss-spacing-l);
}

.ss-content .ss-search input {
    display: inline-flex;
    font-size: inherit;
    line-height: inherit;
    flex: 1 1 auto;
    width: 100%;
    min-width: 0px;
    padding: var(--ss-spacing-m) var(--ss-spacing-l);
    margin: 0;
    border-radius: var(--ss-border-radius);
    background-color: var(--dark-theme-main-text);
    outline: 0;
    text-align: left;
    box-sizing: border-box;
    border: 1px solid rgba(5, 5, 5, 0.20);
}


.ss-content .ss-search input::placeholder {
    vertical-align: middle;
}


.ss-content .ss-search input::-webkit-search-cancel-button {

    -webkit-appearance: none;
}

.ss-content .ss-search .ss-addable {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    flex: 0 0 auto;
    height: auto;
    margin: 0 0 0 var(--ss-spacing-m);
    border-radius: var(--ss-border-radius);
}

.ss-content .ss-search .ss-addable svg {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 0 1 auto;
    width: 12px;
    height: 12px;
    margin: auto var(--ss-spacing-m) auto var(--ss-spacing-m);
}

.ss-content .ss-search .ss-addable svg path {
    fill: none;
    stroke-width: 18;
    stroke-linecap: round;
    stroke-linejoin: round;
}

.ss-content .ss-list {
    flex: 1 1 auto;
    height: auto;
    overflow-x: hidden;
    overflow-y: auto;
}

.ss-content .ss-list .ss-error {
    color: var(--ss-error-color);
    padding: var(--ss-spacing-l);
}

.ss-content .ss-list .ss-searching {
    padding: var(--ss-spacing-l);
}

.ss-content .ss-list .ss-optgroup.ss-close .ss-option {
    display: none !important;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: var(--ss-spacing-m) var(--ss-spacing-l) var(--ss-spacing-m) var(--ss-spacing-l);
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-label-text {
    flex: 1 1 auto;
    font-weight: bold;
    color: var(--ss-font-color);
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label:has(.ss-arrow) {
    cursor: pointer;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions {
    flex: 0 1 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: var(--ss-spacing-m);
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall:hover {
    opacity: .5;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall.ss-selected svg path {
    stroke: var(--ss-error-color);
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall span {
    flex: 0 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 60%;
    text-align: center;
    padding: 0 var(--ss-spacing-s) 0 0;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg {
    flex: 0 1 auto;
    width: 13px;
    height: 13px;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg path {
    fill: none;
    stroke: var(--green-btn);
    stroke-linecap: round;
    stroke-linejoin: round;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg:first-child {
    stroke-width: 5;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-selectall svg:last-child {
    stroke-width: 11;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-closable {
    flex: 0 1 auto;
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-closable .ss-arrow {
    flex: 1 1 auto;
    width: 10px;
    height: 10px;
}

.ss-content .ss-list .ss-optgroup .ss-optgroup-label .ss-optgroup-actions .ss-closable .ss-arrow path {
    fill: none;
    stroke-width: 18;
    stroke-linecap: round;
    stroke-linejoin: round;
    transition-timing-function: ease-out;
    transition: var(--ss-animation-timing);
}

.ss-content .ss-list .ss-optgroup .ss-option {
    padding: var(--ss-spacing-s) var(--ss-spacing-s) var(--ss-spacing-s) calc(var(--ss-spacing-l)*3);
}

.ss-content .ss-list .ss-option {
    display: flex;
    padding: var(--ss-spacing-m) var(--ss-spacing-l) var(--ss-spacing-m) var(--ss-spacing-l);
    cursor: pointer;
    user-select: none;
}

.ss-content .ss-list .ss-option:hover {
    color: var(--dark-theme-main-text);
    background-color: var(--green-btn);
}

.ss-content .ss-list .ss-option.ss-highlighted,
.ss-content .ss-list .ss-option:not(.ss-disabled).ss-selected {
    color: var(--dark-theme-main-text);
    background-color: var(--secondary-color);
}

.swiper-pagination-horizontal{
    display: none;
 }

.swiper-button-next:after,
.swiper-button-prev:after {
    color: var(--primary-color);
    font-size: 24px; 
}

.swiper-scrollbar-drag {
    cursor: pointer;
    background-color: var(--primary-color);
}

.swiper-slide {
    text-align: center;
}

@media screen and (min-width: 360px) {

    .swiper {
            padding: 20px 0px;
            width: auto;
            height: 800px;
        }

    .swiper-button-next {
        right: -7px;
        top: 48%
    }

    .swiper-button-prev {
        left: -7px;
        top: 48%
    }

    .gallery-container {
        padding-top: 24px;
        padding-bottom: 24px;
    }

    .gallery-title {
        font-size: 20px;
        font-weight: 600;
        color: var(--dark-text-color);
    }

    .gallery-select {
        margin-top: 25px;
        width: 133px;
        height: 42px;
        font-size: 16px;
    }
    
    .gallery-item {
        max-width: 100%;
        display: grid;
        justify-items: center;
        justify-content: center;
    }
    
    .gallery-holder {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        width: 296px;
        height: 48px;
        padding: 12px 18px 12px 16px;
        margin-top: 8px;
        background-color: var(--primary-color);
        z-index: 1;
    }
    
    .gallery-subtitle {
        font-size: 18px;
        font-weight: 500;
        text-align: center;
        color: var(--light-text-color);
    }

    .gallery-text{
        font-size: 14px;
        margin-top: 24px;
        text-align: left;
        color: var(--dark-text-color);
    }
}

@media screen and (min-width: 768px) {
    .swiper {
        padding: 20px 10px;
    }

    .swiper-button-next,
    .swiper-button-prev {
        top: 48%;
    }
}

@media screen and (min-width: 1024px) {
    .gallery-container {
        padding-top: 40px;
        padding-bottom: 60px;
    }

    .gallery-title {
        font-size: 32px;
    }

    .swiper {
        padding: 30px;
    }

    .swiper-button-next,
    .swiper-button-prev {
        top: 50%;
    }

    .gallery-select {
        margin-top: 50px;
        width: 170px;
        height: 48px;
        font-size: 19px;
    }
}

@media screen and (min-width: 1024px) {

    .swiper {
        padding: 30px 10px;
    }
  
    .gallery-container {
        padding-top: 24px;
        padding-bottom: 128px;
    }

    .gallery-title {
        font-size: 32px;
    }

    .gallery-subtitle {
        font-size: 19px;
    }

    .gallery-text {
        font-size: 19px;
    }
}